
import { defineComponent, onMounted, ref } from "vue";
import { setCurrentPageTitle } from "@/core/helpers/breadcrumb";
import { Actions } from "@/store/enums/StoreEnums";
import { useStore } from "vuex";
import SettingService from "@/store/modules/setting/service/setting.service";
import ISetting from "@/store/modules/setting/interface/setting.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";

export default defineComponent({
    name: "Layout",
    components: {
    },
    setup() {
        const setting = ref<ISetting>({
            id: null,
            description: '',
            value: '',
            slug: ''
        });
        const settingService = new SettingService();

        const getData = () => settingService.getData()
        .then((response) => {
            setting.value = response.data
        })
        .catch((error) => {
            
        });

        const saveData = () => settingService.update(setting.value.id, setting.value)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });
        
        
        onMounted(() => {
            getData()
        });

        return {
            getData,
            saveData,
            setting
        };
    },
});
